import React, { Component } from 'react';
import data from './data/data.json';
import Select from 'react-select';
import TaskTable from './components/Table/index'


export default class App extends Component {
  constructor() {
    super();
    this.mentorsList = Object.keys(data.mentors);
    this.options = this.mentorsList.map((gitHubName) => ({value : gitHubName, label : gitHubName}));
  }
  state = {
    selectedMentor: localStorage.getItem('mentor') ? {value : localStorage.getItem('mentor'), label : localStorage.getItem('mentor')} : null,
  }
  handleChange = (selectedMentor) => {
    this.setState({ selectedMentor });
    localStorage.setItem('mentor', selectedMentor.value);
  }
  render() {
    const { selectedMentor } = this.state;
    console.log(localStorage.getItem('mentor'));

    return (
      <div>
        <Select
        value={selectedMentor}
        onChange={this.handleChange}
        options={this.options}
      />
      { selectedMentor &&
        <TaskTable 
          tasks={data.tasks}
          students={data.mentors[selectedMentor.value].students}
      />}        
      </div>      
    );
  }
}